import React, { useState, useEffect  } from "react";


const ChangePasswordWindow = (props) => {
  const [password, setPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [newPasswordDouble, setNewPasswordDouble] = useState('');
  const [submitDisabled, setSubmitDisabled] = useState(false);

  const checkNewPassword = ()=>{
    if (!!password && !!newPasswordDouble && !!newPassword && (newPasswordDouble === newPassword)) {
      return setSubmitDisabled (true);
    }
    setSubmitDisabled (false); // –¥–æ–¥–µ–ª–∞—Ç—å –ø—Ä–æ–≤–µ—Ä–∫—É
  };
  useEffect (()=>checkNewPassword(),[password,newPasswordDouble,newPassword] );
  
  return (
    <div
      className="modal-window d-flex fixed-top justify-content-center align-items-center"
      onClick={() => props.closeChPasForm({ changePasswordFormIsOpen: false })}
    >
      <form
        className="registration__form bg-light p-3"
        onClick={e => e.stopPropagation()}
      >
        <h3 className="registration">–°–º–µ–Ω–∏—Ç—å –ø–∞—Ä–æ–ª—å</h3>

        <div className="form-group">
          <label htmlFor="Password">–°—Ç–∞—Ä—ã–π –ø–∞—Ä–æ–ª—å</label>
          <input
            type="password"
            className="form-control"
            id="Password"
            value={password}
            placeholder="–í–≤–µ–¥–∏—Ç–µ c—Ç–∞—Ä—ã–π –ø–∞—Ä–æ–ª—å"

            onChange={(e) => {setPassword(e.target.value);
              // checkNewPassword();
            }}
          />
        </div>
        <div className="form-group">
          <label htmlFor="Password1">–ù–æ–≤—ã–π –ø–∞—Ä–æ–ª—å</label>
          <input
            type="password"
            className="form-control"
            id="Password1"
            value={newPassword}
            placeholder="–í–≤–µ–¥–∏—Ç–µ –Ω–æ–≤—ã–π –ø–∞—Ä–æ–ª—å"

            onChange={(e) => {setNewPassword(e.target.value);
              // checkNewPassword();
            }}
          />
                    <input
            type="password"
            className="form-control mt-1"
            id="Password2"
            value={newPasswordDouble}
            placeholder="–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –Ω–æ–≤—ã–π –ø–∞—Ä–æ–ª—å"
            onChange={(e) => {setNewPasswordDouble(e.target.value);
              // checkNewPassword();

            }}
          />
        </div>
        <button
          type="submit"
          className="btn btn-primary"
          disabled={!submitDisabled}
          onClick={() => {
            props.closeChPasForm({ changePasswordFormIsOpen: false });
            return props.setnewpassword(props.email, password, newPassword);
          }}
        >
          –û—Ç–ø—Ä–∞–≤–∏—Ç—å
        </button>
      </form>
    </div>

  );
};

export { ChangePasswordWindow };