import React, { useState } from "react";


const LoginWindow = (props) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  return (
    <div
      className="modal-window d-flex fixed-top justify-content-center align-items-center"
      onClick={() => props.closeForm({ loginWindowIsOpen: false })}
    >
      <form
        className="registration__form bg-info p-3"
        onClick={e => e.stopPropagation()}
      >
        <h3 className="registration">–í—Ö–æ–¥</h3>
        <div className="form-group ">
          <label htmlFor="exampleInputEmail1">–ê–¥—Ä–µ—Å —ç–ª–µ–∫—Ç—Ä–æ–Ω–Ω–æ–π –ø–æ—á—Ç—ã</label>
          <input
            type="email"
            className="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required={true}
          />
        </div>
        <div className="form-group">
          <label htmlFor="exampleInputPassword1">–ü–∞—Ä–æ–ª—å</label>
          <input
            type="password"
            className="form-control"
            id="exampleInputPassword1"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required={true}

          />
        </div>
        <button
          type="submit"
          className="btn btn-primary"
          onClick={() => {
            props.closeForm({ loginWindowIsOpen: false });
            return props.loginHandler(email, password);
          }}
        >
          –û—Ç–ø—Ä–∞–≤–∏—Ç—å
        </button>
      </form>
    </div>

  );
};

export { LoginWindow };