import React from "react";
import { useDispatch } from "react-redux";
import { initialState, setAllGroupsAC } from "../../redux/ItemListReducer";
import { clearLocStorage } from "../../utils/clearList";

export function ClearButton() {
  const dispatch = useDispatch();
  const handleClick = (e) => {
    e.preventDefault();
    clearLocStorage();
    dispatch(setAllGroupsAC({groups: initialState.groups, deleted: []})); 
  };
    return (
      <button
        className="btn btn-warning rounded-circle button-crear-add d-flex "
        onClick={(e)=>handleClick(e)}
      >
        <svg 
          xmlns="http://www.w3.org/2000/svg" 
          width="1.5em" 
          height="1.5em" 
          fill="currentColor" 
          className="bi bi-arrow-90deg-left" 
          viewBox="0 0 16 16">
          <path fillRule="evenodd" d="M1.146 4.854a.5.5 0 0 1 0-.708l4-4a.5.5 0 1 1 .708.708L2.707 4H12.5A2.5 2.5 0 0 1 15 6.5v8a.5.5 0 0 1-1 0v-8A1.5 1.5 0 0 0 12.5 5H2.707l3.147 3.146a.5.5 0 1 1-.708.708l-4-4z" />
        </svg>
      </button>
    );
  
}
